import React from 'react';
import Grid from 'material-ui/Grid';

const GridBlock = (props) =>  (
  <Grid container>
    <Grid item xs sm={2} md={1} lg={1} />
    <Grid item xs={12} sm={8} md={10} lg={10} >
      {props.children}
    </Grid>
    <Grid item xs sm={2} md={1} lg={1} />
  </Grid>
);
export default GridBlock;



